  handleChange = (e, { value }) => this.setState({ value });

  postResultApi = ({ age, sex, type }) => {
    const headers = {
      Accept: "application/json",
      "Content-Type": "application/json",
      "Client-hostname": `${window.location.hostname}`
    };
    const result = {
      sex,
      age,
      type
    };

    const options = {
      method: "POST",
      headers,
      data: { ...result },
      url: `https://clev.it/api/skin`
    };

    return new Promise((resolve, reject) => {
      axios(options)
        .then(response => response.data)
        .then(data => resolve(data))
        .catch(error => reject(error));
    });
  };

  handleResult = async () => {
    const newType = [...this.state.type];
    const { selectedGender, value } = this.state;
    const selectedCount = newType.filter(data => data.selected === true);
    if (selectedCount.length !== 4) {
      alert("타입을 4개 선택해주세요!");
    } else {
      let typeIdArray = [];
      selectedCount.map(type => {
        typeIdArray.push(type.id);
      });
      const result = await this.postResultApi({
        age: value,
        sex: selectedGender,
        type: typeIdArray
      });
      this.setState({ selectedIndex: 3, finalResult: result.result }, () => {
        window.scrollTo(0, 0);
      });
    }
  };

